const ListTheme = React.lazy(() => import("./ListTheme"));
const GridTheme = React.lazy(() => import("./GridTheme"));
const AutoScrollThemeEnableLoadData = isTagShop ? TagShopLoadData : ASTLoadData
const ShowMoreVisibleThemes =  isTagShop ? TagShopThemes : SMVThemes

const ThemeRender = React.memo(({ loadBranding, renderId, handlePressStart, handlePressEnd, loadMoredata, wall, tuchMobile, reviewFilters, webFilters, postData, adjustWidth, clickToShowPopUp, cardNumber, onClickToCTA, postSizeInPx, onClickPopUpSlider, parentID, keyRender, containerSize, heightUpdate }) => {
  const hasMoreData = postData.hasMoreData[0] ? postData.hasMoreData[0].hasMoreData == false ? false : postData.hasMoreData[postData.appendData.networkID] ? postData.hasMoreData[postData.appendData.networkID].hasMoreData : true : true
  const { CardStyle, ThemeStyle, ProductSetting, ThemeInfo, SnapUp } = wall
  const themeProps = {...postData, loadBranding, handlePressStart, handlePressEnd, heightUpdate, loadMoredata, reviewFilters, webFilters: webFilters, hasMoreData: hasMoreData, adjustWidth: adjustWidth, clickToShowPopUp: clickToShowPopUp, onClickToCTA: onClickToCTA, onClickPopUpSlider, keyRender, containerSize, heightUpdate, cardNumber, postSizeInPx, parentID, renderId, CardStyle, ThemeStyle, ProductSetting, ThemeInfo, SnapUp }

  switch (ThemeInfo.id) {
    // case 20:
    //   return <Suspense fallback={<></>}><ModernCard {...themeProps} /></Suspense>
    // case 5:
    //   return <Suspense fallback={<></>}><ClassicCard {...themeProps} /></Suspense>
    case 3:
      return <Suspense fallback={<></>}><ClassicPhoto {...themeProps} /></Suspense>
    case 4:
      return <Suspense fallback={<></>}><SquarePhoto {...themeProps} /> </Suspense>
    case 19:
    case 59:
    case 83:
      return adjustWidth ? <Suspense fallback={<></>}><NewsCard {...themeProps} /> </Suspense> : null
    case 50:
      return isTagShop ? <Suspense fallback={<></>}><GalleryTheme {...themeProps} /></Suspense> : <Suspense fallback={<></>}><GalleryPhoto {...themeProps} /></Suspense>
    // case 52:
    // case 79:
    //   return  isTagbox && IS_THEME(79) ? <Suspense fallback={<></>}><ModernCard {...themeProps} /></Suspense> : <Suspense fallback={<></>}><SinglePost {...themeProps} /></Suspense>
    case 16:
      return <Suspense fallback={<></>}><HorizontalSlider {...themeProps} /></Suspense>
    case 55:
    case 81:
      return <Suspense fallback={<></>}><HighlightTheme {...themeProps} /></Suspense>
    case 49:
    case 53:
    case 85:
      return <Suspense fallback={<></>}> {isTagShop ? <CollageTheme {...themeProps} /> : <WidgetTheme {...themeProps} />}</Suspense>
    //return <Suspense fallback={<></>}><GridTheme {...themeProps} /><ListTheme {...themeProps} /></Suspense>
    case 47:
      return <Suspense fallback={<></>}><HorizontalColumns tuchMobile={tuchMobile} {...themeProps} /></Suspense>
    // case 54:
    // case 80:
    //   return <Suspense fallback={<></>}><ReviewCarousel {...themeProps} /></Suspense>
    case 6:
    case 61:
      return <Suspense fallback={<></>}><ReelTheme {...themeProps} /></Suspense>
    case 62:
      return isTagShop ? <Suspense fallback={<></>}><ReelShop {...themeProps} /></Suspense> : isSocialWall ? <Suspense fallback={<></>}>{/* <Cascade {...themeProps} /><ClassicCard {...themeProps} /> */}</Suspense>: ''
    // case 57:
    // case 84:
    //   return <Suspense fallback={<></>}><Badge {...themeProps} /></Suspense>
    // case 58:
    // case 82:
    //   return <Suspense fallback={<></>}><RatingBadge {...themeProps} /></Suspense>
    // case 60:
    //   return <Suspense fallback={<></>}><StoryTheme {...themeProps} /></Suspense>
    // case 56:
    //   return <Suspense fallback={<></>}><StoryTheme {...themeProps} /></Suspense>
    // case 63:
    //   return <Suspense fallback={<></>}>
    //     <Focus {...themeProps} />
    //   </Suspense>
    // case 64:
    //   return <Suspense fallback={<></>}><Exhibit {...themeProps} /></Suspense>
    // case 65:
    //   return <Suspense fallback={<></>}>
    //     <Pixel {...themeProps} />
    //   </Suspense>
    // case 78:
    //   return <Suspense fallback={<></>}>
    //     <RoundEdge {...themeProps} />
    //   </Suspense>
    // case 66:
    //   return <Suspense fallback={<></>}>
    //     <GSquare {...themeProps} />
    //   </Suspense>
    // case 67:
    //   return <Suspense fallback={<></>}>
    //     <GPixel {...themeProps} />
    //   </Suspense>
    // case 68:
    //   return <Suspense fallback={<></>}>
    //     <GFocus {...themeProps} />
    //   </Suspense>
    // case 69:
    //   return <Suspense fallback={<></>}><GReels {...themeProps} /></Suspense>
    // case 74:
    // case 76:
    // case 77:
    //   return <Suspense fallback={<></>}><SlideShow {...themeProps} /></Suspense>
    // case 70:
    //   return <Suspense fallback={<></>}>
    //     <CHorizontal {...themeProps} />
    //   </Suspense>
    // case 71:
    //   return <Suspense fallback={<></>}>
    //     {isSocialEmbed ? <HorizontalSlider {...themeProps} /> : <CMotion {...themeProps} />}
    //   </Suspense>
    // case 73:
    //   return <Suspense fallback={<></>}><CReels {...themeProps} /></Suspense>
    // case 58:
    // case 82:
    //   return <Suspense fallback={<></>}><RatingBadge {...themeProps} /></Suspense>
    // case 60:
    //   return <Suspense fallback={<></>}><StoryTheme {...themeProps} /></Suspense>
    // case 56:
    //   return <Suspense fallback={<></>}><StoryTheme {...themeProps} /></Suspense>
    case 63:
      return <Suspense fallback={<></>}><GridTheme {...themeProps} /></Suspense>
    case 64:
      return <Suspense fallback={<></>}><ListTheme {...themeProps} /></Suspense>
    default:
      return <></>
  }
})
class ThemeRoute extends PureComponent {
  state = {
    isLoaded: false, 
    currentIndex: -1,
    tuchMobile : false,
    startPos: { x: 0, y: 0 },
    isBranding : false
  }

  componentDidMount() {
    if (isSocialWall && !isEmbed && !onsiteUpload) {
      const { wall: { ThemeRule: { timeForPopup }, wall } } = this.props;
      const timer = timeForPopup === 0 ? 5000 : timeForPopup * 1000;
      if (wall.ThemeStyle.modal.action == 1) setTimeout(() => this.loadPopUpWithTimer(), timer)
    }
    if (window.editor) document.querySelector(".wall-editor")?.addEventListener("scroll", this.autoLoadEditorScrollData);
    else {
      window.addEventListener && window.addEventListener("scroll", this.autoLoadScrollData);
    }
    setTimeout(() => {
      this.setState({
        isLoaded: true
      })
    }, 2000);
    if (isTagShop && !TagShopEditor) reelPlayTimeUpdate()
  }

  componentDidUpdate(prevProps,preState){
    if(detectMobile() && (preState.tuchMobile!==this.state.tuchMobile)) this.props.loadMoredata()
  }

  loadBranding=()=> this.setState({ isBranding :true }) 

  handleTouchStart = (e) => this.setState({ tuchMobile : true })  

  autoLoadEditorScrollData = event => {
    const { wall, postData, loaderData } = this.props;
    const postCount = wall.ThemeStyle.totalPosts;
    let wallEditor = document.getElementsByClassName("wall-editor");
    if (wall.ThemeStyle.autoScrollStatus == 1 && AutoScrollThemeEnableLoadData.includes(wall?.ThemeInfo.id) && wallEditor.length > 0) {
      let fixedHeightScrollBar = wallEditor[0];
      let documentHeight = document.body.scrollHeight;
      if ((documentHeight + fixedHeightScrollBar.scrollTop) >= (fixedHeightScrollBar.scrollHeight - 10)) {
        if (!(loaderData && loaderData.isShowMoreLoading)) {
          const timeStamp = Math.floor(Date.now() / 1000);
          this.props.nextData(isTagShop ? wall.ThemeInfo.webId : wall.ThemeInfo.wallId, timeStamp, postCount, postData.appendData.networkID, postData.appendData.after, postData.appendData.heightEvent);
        }
      }
    }
  }
  
  autoLoadScrollData = event => {
    const { wall, postData, loaderData } = this.props;
    const postCount = wall.ThemeStyle.totalPosts;
    const body = document.body;
    const html = document.documentElement;
    if (wall.ThemeStyle.autoScrollStatus === 1 && AutoScrollThemeEnableLoadData.includes(wall?.ThemeInfo.id)) {
      const height = Math.max(body.scrollHeight, body.offsetHeight, html.clientHeight, html.scrollHeight, html.offsetHeight);
      if ((window.innerHeight + window.pageYOffset) >= (parseInt(height) - parseInt(50))) {
        if (!(loaderData && loaderData.isShowMoreLoading)) {
          const timeStamp = Math.floor(Date.now() / 1000);
          this.props.nextData(isTagShop ? wall.ThemeInfo.webId : wall.ThemeInfo.wallId, timeStamp, postCount, postData.appendData.networkID, postData.appendData.after, postData.appendData.heightEvent);
        }
      }
    }
  }

  onClickToShowPopup = (currentIndex, item) => (event) => {  
    // if(!detectMobile() && (!this.state.tuchMobile)) this.setState({ tuchMobile : true },()=>this.props.loadMoredata()) 
    event.stopPropagation();
    this.onLoadPopUp(currentIndex, item)
    //
  }
  onClickPopUpSlider = (currentIndex, item) => this.onLoadPopUp(currentIndex, item)
  onLoadPopUp = (currentIndex, item) => {
    const { wall, postData, } = this.props;
    const { viewOn, share } = wall.ThemeStyle.language.text
    const isUserType = wall?.User?.userDatabaseType == "tagembed"
    const ownerId = wall.ThemeInfo?.ownerId
    const wallId = isTagShop ? wall.ThemeInfo?.webId : wall.ThemeInfo?.wallId
    const modal = wall.ThemeStyle?.modal
    if (!isUserType && !isTagShop && ((detectMobile() && !modal?.mobile) || ((isUserType && modal?.mobile) && item.link !== ""))) {
      if (item.link == 'undefined') return false
      window.open(item.link, '_blank')
    }
    else {
      let updatePostData = postData.postData.map(pItem => postData.completeDataObject[pItem])
      let filteredPostData = updatePostData.filter((filterData) => !String(filterData.id).includes("free_add_"))
      let nIndex = findFromArray(filteredPostData, item.id, currentIndex)
      if (modal?.action === 2) {
        if (item.link == 'undefined' || item.link == '') return false
        window.open(item.link, '_blank')
      }
      else if (modal?.action === 3) window.open(modal.redirect, '_blank')
      else {
        if (modal?.action === 1) this.props.showPopUP({ type: 'post', card: item, idArray: filteredPostData, index: nIndex, viewOnText: viewOn, shareText: share, imgData: URLDATA, wall: wall })
      }
    }
    const requestData = { feed: item.feedId, post: item.referenceId ? item.referenceId : item.id }
    const ugcPayload = { action: 2, wall: wallId, feed: item.feedId, post: item.referenceId ? item.referenceId : item.id, type: 3, owner: ownerId }

    themePostTracking(suiteTrack ? ugcPayload : { action: 2, wall: wallId, ...requestData, owner: ownerId })
    gtmAnalytics("Tagbox", "popupclick_", "popup", item.link)
  }

  loadPopUpWithTimer = () => {
    const { currentIndex } = this.state;
    const { postData: { postData, completeDataObject }, wall: { ThemeRule: { timeForPopup } } } = this.props;
    const timer = timeForPopup === 0 ? 5000 : timeForPopup * 1000;
    let nextIndex = currentIndex + 1;
    if (nextIndex >= postData.length) nextIndex = 0;
    if (!isNaN(nextIndex) && postData.length > 0) {
      const updatePostData = postData.map((pItem) => completeDataObject[pItem]);
      this.setState({ currentIndex: nextIndex });
      this.togglePopUpVisibility(nextIndex, updatePostData[nextIndex], timer);
    }
  }

  togglePopUpVisibility = (index, data, timer) => {
    this.onLoadPopUp(index, data);
    setTimeout(() => {
      this.props.closePopUP();
      setTimeout(() => {
        this.loadPopUpWithTimer();
      }, timer);
    }, timer);
  };


  onClickToCTA = (item) => {
    const { wall } = this.props;
    themePostTracking({ action: 2, wall: wall.ThemeInfo.wallId, type: 1, feed: item.feedId, post: item.referenceId ? item.referenceId : item.id, owner: wall.ThemeInfo.ownerId })
    gtmAnalytics("Tagbox", "ctaClick", "cta", item.cta && item.cta.url ? item.cta.url : "")
  }

    // Record the start position on mouse or touch down
    handlePressStart = (e) => {
      const isHotspot = e.target.getAttribute("hotspot")
      const point = e.touches ? e.touches[0] : e;
      !isHotspot && this.setState({ startPos: { x: point.clientX, y: point.clientY } });
    };

    // Determine if the movement qualifies as a click
    handlePressEnd = (e, index, data) => {
      const isHotspot = e.target.getAttribute("hotspot")
      if(!isHotspot){
        const point = e.changedTouches ? e.changedTouches[0] : e;
        const endPos = { x: point.clientX, y: point.clientY };
        const { startPos } = this.state;
        const deltaX = Math.abs(endPos.x - startPos.x);
        const deltaY = Math.abs(endPos.y - startPos.y);
        const threshold = 5; // pixels

        if (deltaX < threshold && deltaY < threshold) {
          // Consider this a click
          this.onClickToShowPopup(index, data)(e)
        }
      }
    };


  render() {
    const { wall, postData, containerSize, brandingNetworks } = this.props;
    const { isLoaded, height, isBranding } = this.state
    const totalPost = postData?.postData?.length
    const brandingStatus = wall?.ThemeInfo?.branding && totalPost && isBranding
    const seeMoreStatus = isLoaded && wall?.ThemeStyle?.loadMoreStatus === 1 && ShowMoreVisibleThemes.includes(wall?.ThemeInfo.id) && isBranding && totalPost ? true : false

    return <>
      <div onTouchStart={this.handleTouchStart} className={`tb_theme_container ${isSocialWall && !isSocialEmbed ? 'tb_wall_container' : ''}`} style={{ height: isSocialWall && !isSocialEmbed ? `calc(100vh - ${height}px)` : null }}>
        { brandingStatus && isTagbox ? <Suspense fallback={<></>}><ThemeBranding wall={wall} containerSize={containerSize}  brandingNetworks={brandingNetworks} wallId={wall.ThemeInfo.wallId} themeID={wall?.ThemeInfo.id} user={wall.User.userDatabaseType} /></Suspense> : null}
        <ThemeRender {...this.props} {...this.state} loadBranding={this.loadBranding} handlePressStart={this.handlePressStart} handlePressEnd={this.handlePressEnd} clickToShowPopUp={this.onClickToShowPopup} onClickToCTA={this.onClickToCTA} onClickPopUpSlider={this.onClickPopUpSlider} onLoadPopUp={this.onLoadPopUp}
          {...(isSocialWall && {heightUpdate: this.heightUpdate, key: totalPost })}
        />
        {brandingStatus && !isTagbox ?
        <Suspense fallback={<></>}><ThemeBranding wall={wall} containerSize={containerSize} brandingNetworks={brandingNetworks} wallId={wall.ThemeInfo.wallId} themeID={wall?.ThemeInfo.id} /></Suspense> 
        : null}
        {seeMoreStatus ? <Suspense fallback={<></>}><SeeMore ThemeStyle={wall.ThemeStyle} ThemeInfo={wall.ThemeInfo} postData={postData} /></Suspense> : null}
      </div>
    </>
  }
}


const mapStateToProps = state => {
  return {
    loaderData: state.loaderData,
    renderId: state.renderId
  }
}
const mapDispatchToProps = dispatch => {
  return {
    showPopUP: (data) => dispatch(showPopUP(data)),
    nextData: (wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow) => dispatch(nextData(wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow)),
    closePopUP: () => dispatch(closePopUP())
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(ThemeRoute);